import React, { useState } from 'react'
import { useForm, SubmitHandler } from "react-hook-form";
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from 'yup';
import { useQuery } from 'react-query';
import axios from 'axios';
import { nanoid } from 'nanoid'
import { useAppDispatch } from '../store/hooks';
import { closeModal } from '../store/modalSlice';


type FormValues = {
    name: string;
    email: string;
    text: string;
};

export default function QuestionForm({ setFormResponse }: any) {
    const [validation, setValidation] = useState(false);
    const dispatch = useAppDispatch();
    let validationClassName = "";
    if (validation) {
        validationClassName = "validation"
    }

    const schema = yup.object().shape({
        name: yup.string().required("  "),
        email: yup.string().email().required("  e-mail,  "),
        text: yup.string().required("   "),
    })

    const { register, handleSubmit, formState: { errors }, reset } = useForm<FormValues>({
        resolver: yupResolver(schema)
    });

    const onSubmit: SubmitHandler<FormValues> = (data: FormValues) => {
        reset();
        dispatch(closeModal('question'))

        const fetchData = {
            data: data,
            id: nanoid()
        }
        axios.post('http://localhost:7777/questions', fetchData).then((res) => { setFormResponse('   .       ') }).catch((err) => setFormResponse(',    .   '))
    }

    return (
        <form onSubmit={(handleSubmit(onSubmit))}>
            <ul className="list">
                <li>
                    <label htmlFor="modal-name">      ,    </label>
                    <input className={`${validationClassName} ${(Object.keys(errors).length === 0 || errors.name) ? 'invalid' : 'valid'}`} placeholder="" {...register('name')} onChange={() => { setValidation(true) }} />
                    <strong className="modal-question__error-message">{errors.name?.message}</strong>
                </li>
                <li>
                    <label htmlFor="modal-email">   ,   e-mail </label>
                    <input className={`${validationClassName} ${(Object.keys(errors).length === 0 || errors.email) ? 'invalid' : 'valid'}`} placeholder="E-mail" {...register('email')} onChange={() => { setValidation(true) }} />
                    <strong className="modal-question__error-message">{errors.email?.message}</strong>
                </li>
                <li>
                    <label htmlFor="modal-text"> </label>
                    <textarea className={`${validationClassName} ${(Object.keys(errors).length === 0 || errors.text) ? 'invalid' : 'valid'}`} placeholder=" ..." {...register('text')} onChange={() => { setValidation(true) }} />
                    <strong className="modal-question__error-message">{errors.text?.message}</strong>
                </li>
            </ul>
            <div className="modal-question__form-container">
                <label htmlFor="modal-checkbox">
                    <input className="visually-hidden" type="checkbox" id="modal-checkbox" name="terms" defaultChecked />
                    <span className="modal-question__checkbox-indicator"></span>
                       <a className="modal-question__rules" href="#">    </a> 
                     
                </label>
                <button type="submit" onClick={() => { setValidation(true) }}> </button>
            </div>
        </form>
    )
}
