


export default function setTranslatedMonth(month: number) {
    let monthTranslated;
    switch (month) {
        case 0:
            monthTranslated = '';
            break;
        case 1:
            monthTranslated = '';
            break;
        case 2:
            monthTranslated = '';
            break;
        case 3:
            monthTranslated = '';
            break;
        case 4:
            monthTranslated = '';
            break;
        case 5:
            monthTranslated = '';
            break;
        case 6:
            monthTranslated = '';
            break;
        case 7:
            monthTranslated = '';
            break;
        case 8:
            monthTranslated = '';
            break;
        case 9:
            monthTranslated = '';
            break;
        case 10:
            monthTranslated = '';
            break;
        case 11:
            monthTranslated = '';
            break;

        default:
            monthTranslated = '';

    }
    return monthTranslated;
}
